import React from "react";
import ReactDOM from "react-dom";

class Formpage extends React.Component{
	constructor(){
		super()
		this.state={
			firstName:"",
			secName:"",
			email:"",
			isfriendly:false
		}
		this.handlechange=this.handlechange.bind(this)
	}
	
	handlechange(event){
		
		const{name,value,type,checked}=event.target
		type==="checkbox"?this.setState({ [name]:checked}):
		this.setState({	[name]:value})
	}
	render(){
		return(
			<form  id="form"><h3>Entering and Displaying details using Form</h3>
			<div className="enterdiv"><input name="firstName" onChange={this.handlechange}  type="text" placeholder="firstName" /><br/>
			<input onChange={this.handlechange} type="text" name="secName" placeholder="secName" /><br/>
			<input onChange={this.handlechange} type="text" name="email" placeholder="email" /><br/>
			<textarea  onChange={this.handlechange}  name="address" placeholder="address"/><br/>
			<select value={this.state.state} onChange={this.handlechange} name="state"><option value="Ontario">Ontario</option><option value="BC">BC</option><option value="Montreal">Montreal</option></select><br/>
			<input type="radio" name="gender" value="female" checked={this.state.gender==="female"} onChange={this.handlechange}/>Female<br/>
			<input type="radio" name="gender" value="male" checked={this.state.gender==="male"} onChange={this.handlechange}/>Male<br/>
			<input type="checkbox" onChange={this.handlechange}  name="isfriendly" checked={this.state.isfriendly} />Is Friendly<br/>
			</div><div className="displaydiv">
			<label>{this.state.firstName}</label>
			<label>{this.state.secName}</label>
			<label>{this.state.email}</label>
			<label>{this.state.address}</label>
			<label>{this.state.state}</label>
			<label>{this.state.gender}</label>
			</div>
			</form>
		)
	}
}
export default Formpage