import React from "react";
import ReactDOM from "react-dom";
import Nav from "./nav";

class Headernew extends React.Component{
	constructor(){
		super()
		this.state={
			
			isLoggedIn:false
			
		}
		this.handleclick=this.handleclick.bind(this)
	}
	
	handleclick(event){
		this.setState(prevState=>{
			
			return{
				
			isLoggedIn:!prevState.isLoggedIn
				
			}
		})
	}
	
	
	render(){
		const btnvalue = this.state.isLoggedIn ? "LOG OUT" : "LOG IN"
		const btntext = this.state.isLoggedIn ? "Logged in" : "Logged out"
		return(<div><header><h1>React Components</h1>
	<Nav /><div className="login"><span>You are {btntext}</span>&nbsp;<span><button  onClick={this.handleclick}>{btnvalue}</button></span></div>
	</header></div>
	) 
	}
	
}
export default Headernew 