import React from "react";
import ReactDOM from "react-dom";
import Todoitem from "./todoitem";
import tododata from "./todoitemdata";



function Todo(){
	
	const Todolist = tododata.map(items=> <Todoitem key={items.id} text={items.text} completed={items.completed} />)
	
	
	return(<div className="tododiv"><h3>To-do list</h3><div className="todoitem">
		{Todolist}
		</div></div>
		)
}

export default Todo