import React from "react";
import ReactDON from "react-dom";
import contactdata from "./employeedata";
import Contact from "./Contacts";

function Contactdet(){
	const contactcomponents=contactdata.map(cont=> <Contact name={cont.name}	age={cont.age} key={cont.id}  email={cont.email} phone={cont.phone} imageurl={cont.imageurl} />)
	
	
	return (<div className="empdiv" id="employee" ><h3>Employee Details</h3>
<div className="itemdiv">{contactcomponents }</div>
</div>
	)
}

export default Contactdet