import React from "react";
import ReactDOM from "react-dom";

function Conditionalnew (props){
	return(
	<div className="conditional">
	{props.isLoading===true ?<h4> Loading.... </h4> :
	
	<h4>Conditional rendering happening here..</h4>}
	
	</div>
	)
	
}

export default Conditionalnew