import React from "react";
import ReactDOM from "react-dom";
import Conditionalnew from "./conditional";


class Conditional extends React.Component{
	
	constructor(){
		super()
		
		this.state={
			isLoading:true
		}
	}
	componentDidMount(){
		setTimeout(()=>{
			this.setState({
				isLoading:false
			})
			
			},1500)
	}
	
	render(){
		return(
		<div > <Conditionalnew isLoading={this.state.isLoading}/>
		</div>
		)
	}
}

export default Conditional